		"description": "This spell wards a willing creature you touch and creates a\nmystic connection between you and the target until the\nspell ends. While the target is with in 60 feet of you, it gains\na+1 bonus to AC and saving throws, and it has resistance\nto all damage. Also, each time it takes damage, you take\nthe same amount of damage.\nThe spell ends if you drop to 0 hit points or if you and the\ntarget become separated by more than 60 feet. It also\nends if the spell is cast again on either of the connected\ncreatures. You can also dismiss the spell as an action\nX\nBMW SERIE 5 2015\nPRIX TOUT INCLUS\nII\n",
