		"description": "You create a magical zone that guards against deception\nin a 15-foot-radius sphere centered on a point of your\nchoice within range. Until the spell ends, a creature that\nenters the spell's area for the first time on a turn or starts\nits turn there must make a Charisma saving throw. On a\nfailed save, a creature can't speak a deliberate lie while in\nthe radius. You know whether each creature succeeds or\nfails on its saving throw.\nAn affected creature is aware of the spell and can thus\navoid answering questions to which it would normally\nrespond with a lie. Such a creature can be evasive in its\nanswers as long as it remains within the boundaries of the\ntruth\n",
		"save": "Charisma"
